body {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
    margin: 0;
    padding: 0;
}

ul {
    list-style: none;
    font-style: normal;
}

a {
    text-decoration: none;
}

img {
    margin-top: 0;
    margin-bottom: 0;
    display: block;
}